import React, { Component } from "react";
import dayjs from "dayjs";
import "dayjs/locale/uk";
import "./App.css";
import io from "socket.io-client";
import {
  SUCCESS,
  TIMER_DATA,
  CAPTURE_DATA,
  LAYOUT,
} from "./utils/CONST_events";
import MainLayout from "./components/MainLayout";
import BigTimeLayout from "./components/bigTimeLayout";

dayjs.locale("uk");

class App extends Component {
  state = {
    date: "",
    timer: {
      minutes: "00",
      seconds: "00",
    },
    capture: {
      hours: "00",
      minutes: "00",
    },
    flash: false,
    connected: false,
    timeIsUp: false,
    bigTimeLayout: true,
  };

  getDate() {
    setInterval(() => {
      const date = dayjs().locale("uk").format("dddd D MMM HH:mm");
      this.setState({ date: date.toUpperCase().split(" ") });
    }, 5000);
  }

  static getDerivedStateFromProps(props, state) {
    const date = dayjs().locale("uk").format("dddd D MMM HH:mm");
    return {
      ...state,
      date: date.split(" "),
    };
  }

  componentDidMount() {
    if (!this.state.connected) {
      this.socket = io("/timer", {
        transports: ["websocket"],
      });

      this.socket.on(SUCCESS, () => {
        this.getDate();
        this.setState({ connected: true });
      });

      this.socket.on("connect_error", () => {
        this.setState({ connected: false });
      });
      this.socket.on("disconnect", () => {
        this.setState({ connected: false });
      });

      this.socket.on(TIMER_DATA, (data) => {
        if (data.minutes < 0) {
          this.setState({ flash: !this.state.flash });
        } else {
          this.setState({ flash: false });
        }
        this.setState({ timer: data });
      });
      this.socket.on(CAPTURE_DATA, (data) => {
        this.setState({ capture: data });
      });
      this.socket.on(LAYOUT, () => {
        this.setState({ bigTimeLayout: !this.state.bigTimeLayout });
      });
    }
  }

  generateTimer() {
    return (
      <h1>
        {this.state.timer.minutes}:{this.state.timer.seconds}
      </h1>
    );
  }

  generateCapture() {
    return (
      <h1>
        {this.state.capture.hours}:{this.state.capture.minutes}
      </h1>
    );
  }

  render() {
    return this.state.connected ? (
      this.state.bigTimeLayout ? (
        <BigTimeLayout
          flash={this.state.flash}
          date={this.state.date}
          Timer={this.generateTimer()}
          Capture={this.generateCapture()}
          timeIsUp={this.state.timeIsUp}
        />
      ) : (
        <MainLayout
          flash={this.state.flash}
          date={this.state.date}
          Timer={this.generateTimer()}
          Capture={this.generateCapture()}
          timeIsUp={this.state.timeIsUp}
        />
      )
    ) : (
      <div className="root">
        <h1 style={{ color: "white" }}>Waiting</h1>
      </div>
    );
  }
}

export default App;
